body {
  color: #adaeb1;
  font-size: 16px;
  font-family: "Montserrat";
}

.inner {
  max-width: 1920px;
  padding: 0 15px;
  position: relative;
  margin: 0 auto;
}

.menu-btn {
  display: inline-block;
  text-align: right;
  width: 50px;
  height: 40px;
  font-size: 0;
  background-color: transparent;
}

.menu-btn__line-small {
  display: inline-block;
  width: 20px;
  height: 4px;
  background-color: #ffffff;
  margin-bottom: 6px;
}

.menu-btn__line {
  display: inline-block;
  width: 40px;
  height: 4px;
  background-color: #ffffff;
  margin-bottom: 6px;
}

.menu-btn__line-big {
  display: inline-block;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
}

.title-lg {
  font-family: "Montserrat";
  text-transform: uppercase;
  font-size: 18px;
  color: #8b9bf7;
}

.title-md {
  font-size: 17px;
  color: #8a8b8f;
}

.btn-back {
  padding: 21px 5px;
  min-width: 110px;
  border: 1px solid #ebebeb;
  color: #adaeb1;
  background-color: transparent;
  border-radius: 6px;
  transition: border-radius 0.3s;
}

.btn-back:hover {
  border-radius: 15px;
}

@media only screen and (max-width: 450px) {
  .btn-back {
    width: 100%;
  }
}

.btn-create {
  padding: 21px 5px;
  min-width: 205px;
  background-color: #647af4;
  color: #ffffff;
  text-transform: uppercase;
  border-radius: 6px;
  transition: opacity 0.3s, border-radius 0.3s;
}

.btn-create:hover {
  opacity: 0.8;
  border-radius: 15px;
}

@media only screen and (max-width: 450px) {
  .btn-create {
    width: 100%;
  }
}

.header {
  background-color: #3d4196;
  padding: 45px 0 280px;
  position: relative;
}

.header__icons {
  display: flex;
  margin-right: 270px;
  justify-content: flex-end;
}

@media only screen and (max-width: 767px) {
  .header__icons {
    justify-content: flex-start;
    margin-right: auto;
  }
}

.header__icon {
  position: relative;
}

.header__icon:not(:last-child) {
  margin-right: 85px;
}

@media only screen and (max-width: 767px) {
  .header__icon:not(:last-child) {
    margin-right: 30px;
  }
}

