}

.form-apartament {
  position: relative;
}

.form-apartament__field {
  width: 100%;
  padding: 25px 5px 22px 20px;
  background-color: #f8f8f8;
  border-bottom: 1px solid #696969;
}

.form-apartament__field::placeholder {
  color: #696969;
  font-family: "Montserrat";
  font-size: 12px;
}

.form-apartament__required {
  position: absolute;
  top: 73px;
  left: 20px;
  font-size: 12px;
}

.form-checkbox__block {
  position: relative;
  display: block;
  margin-bottom: 40px;
}

.form-checkbox__label-top {
  padding: 20px 75px 20px 0;
  display: inline-block;
}

.form-checkbox__label {
  background-color: #f8f8f8;
  border-radius: 20px;
  padding: 20px 25px;
  margin-right: 80px;
  display: inline-block;
  vertical-align: middle;
}

@media only screen and (max-width: 767px) {
  .form-checkbox__label {
    padding: 10px 15px;
  }
}

.form-checkbox__field {
  display: none;
}

.form-checkbox__info {
  margin-bottom: 80px;
  color: #8a8b8f;
  font-weight: 500;
}

@media only screen and (max-width: 767px) {
  .form-checkbox__info {
    margin-bottom: 30px;
  }
}

.form-checkbox__field:not(:checked) + .form-checkbox__indicator:before,
.form-checkbox__field:checked + .form-checkbox__indicator:before {
  content: '';
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  height: 28px;
  width: 72px;
  border-radius: 15px;
}

@media only screen and (max-width: 767px) {
  .form-checkbox__field:not(:checked) + .form-checkbox__indicator:before,
  .form-checkbox__field:checked + .form-checkbox__indicator:before {
    height: 14px;
    width: 36px;
  }
}

.form-checkbox__field:not(:checked) + .form-checkbox__indicator:before {
  background-color: #f2f2f3;
}

.form-checkbox__field:checked + .form-checkbox__indicator:before {
  background-color: #c4cdff;
}

.form-checkbox__field:not(:checked) + .form-checkbox__indicator:after {
  content: '';
  position: absolute;
  border-radius: 50%;
  right: 30px;
  top: 50%;
  transform: translateY(-50%);
  width: 40px;
  height: 40px;
  background-color: #f2f2f3;
  transition: all 0.3s;
}

@media only screen and (max-width: 767px) {
  .form-checkbox__field:not(:checked) + .form-checkbox__indicator:after {
    width: 20px;
    height: 20px;
    right: 15px;
  }
}

.form-checkbox__field:checked + .form-checkbox__indicator:after {
  content: '';
  position: absolute;
  border-radius: 50%;
  right: -5px;
  top: 50%;
  transform: translateY(-50%);
  width: 40px;
  height: 40px;
  background-color: #647af4;
  transition: all 0.3s;
}

@media only screen and (max-width: 767px) {
  .form-checkbox__field:checked + .form-checkbox__indicator:after {
    width: 20px;
    height: 20px;
    right: -3px;
  }
}

